@import url(https://fonts.googleapis.com/css?family=Roboto:100,100i,300,300i,400&display=swap);
.todo-item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 60px;
  width: 100%;
  border-bottom: 1px solid #cecece;
  font-family: Roboto, sans-serif;
  font-size: 15px;
  color: #333;
}

.todo-text {
  font-size: 1.7em;
  font-weight: 300;
  cursor: pointer;
}

.todo-complete-text {
  text-decoration: line-through;
  color: #cdcdcd;
  font-style: italic;
}

.delete-btn {
  margin-left: auto;
  background: none;
  padding-right: 1.5em;
  outline: none;
  color: #96373778;
  cursor: pointer;
  border: none;
  font-size: 1.5em;
  vertical-align: middle;
}

.delete-btn:active,
.delete-btn:hover {
  color: rgba(150, 55, 55, 0.9);
}

.todo-input {
  display: flex;
  width: 40%;
  padding: 30px;
  margin: -2em auto 0.1em auto;
  outline: 0;
  background: #fefefe;
  border: none;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15), 0 10px 0 -5px #eee;
  font-size: 1.7em;
  font-weight: 300;
  text-indent: 25px;
}

.todo-input::-webkit-input-placeholder {
  color: #cdcdcd;
  font-style: italic;
  opacity: 0.8;
  letter-spacing: 0.02em;
  font-weight: 300;
}

.todo-input:-ms-input-placeholder {
  color: #cdcdcd;
  font-style: italic;
  opacity: 0.8;
  letter-spacing: 0.02em;
  font-weight: 300;
}

.todo-input::-ms-input-placeholder {
  color: #cdcdcd;
  font-style: italic;
  opacity: 0.8;
  letter-spacing: 0.02em;
  font-weight: 300;
}

.todo-input::placeholder {
  color: #cdcdcd;
  font-style: italic;
  opacity: 0.8;
  letter-spacing: 0.02em;
  font-weight: 300;
}

:root {
  --dark-brown: #96373778;
  --button-padding: 0.4em 1.3em;
  --button-margin: 0.4em 0 0 0.4em;
  --complete-btn-padding: 0.4em 0.7em;
}

.todo-list {
  background-color: #fefefe;
  margin: auto;
  width: 40%;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 
  /* The top layer shadow */ 0 1px 1px rgba(0, 0, 0, 0.15),
    /* The second layer */ 0 10px 0 -5px #eee,
    /* The second layer shadow */ 0 10px 1px -4px rgba(0, 0, 0, 0.15),
    /* The third layer */ 0 20px 0 -10px #eee,
    /* The third layer shadow */ 0 20px 1px -9px rgba(0, 0, 0, 0.15);
  padding: 0 30px 30px 30px;
}

.header-text {
  text-align: center;
  color: #96373778;
  color: var(--dark-brown);
  font-weight: 100;
  font-size: 6em;
  margin-top: 0.2em;
}

.footer-container {
  display: flex;
  flex-direction: row;
  height: 2em;
  width: 100%;
  justify-content: center;
}

.todos-count {
  font-weight: 100;
  margin-right: auto;
}

.filters {
  margin: 0;
  padding: 0;
  position: relative;
  right: 0;
  left: -4.7em;
  top: 0.4em;
}

.all-btn,
.active-todo-btn,
.complete-btn {
  background: none;
  outline: 0;
  cursor: pointer;
  font-size: 1em;
  font-weight: 100;
  padding: 0.4em 1.3em;
  padding: var(--button-padding);
}

.active-btn-border {
  border-color: #96373778;
  border-color: var(--dark-brown);
}

.active-todo-btn,
.complete-btn {
  margin: 0.4em 1em 0 0.4em;
}

@media all and (min-width: 320px) {
  .todo-input,
  .todo-list {
    width: 115%;
  }
  .filters {
    left: 0;
  }
  .active-todo-btn,
  .complete-btn {
    margin: 0.4em 0 0 0.4em;
    margin: var(--button-margin);
  }
  .complete-btn {
    padding: 0.4em 0.7em;
    padding: var(--complete-btn-padding);
  }
}

